<template>
  <div>
    <section class="login">
      <div class="container">
        <div class="row justify-content-center">
          <div class="col-md-7 col-lg-5">
            <div class="login-wrap p-3 p-md-5">
              <div class="d-flex align-items-center justify-content-center" id="logo">
                <router-link :to="{name:'home'}">
                  <img src="~@/assets/img/logo.png" alt="logo" class="img-fluid">
                </router-link>
              </div>
              <div class="row">
                <div class="col" v-bind:class="{active:isActive}">
                  <router-link :to="{name:'loginAccount'}" @click.native.prevent="changeLoginMethod('loginAccount')">
                    <h4>账号登录</h4>
                  </router-link>
                </div>
                <div class="col" v-bind:class="{active:!isActive}">
                  <router-link :to="{name:'loginPhone'}" @click.native.prevent="changeLoginMethod('loginPhone')">
                    <h4>手机号登录</h4>
                  </router-link>
                </div>
              </div>
              <router-view></router-view>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
  export default {
    name: 'Login',
    data() {
      return {
        isActive: true,
      }
    },
    methods: {
      changeLoginMethod(method) {
        this.isActive = !this.isActive
        this.$router.push({
          name: method
        })
      }
    }
  }
</script>

<style scoped>
  .login {
    min-height: 720px;
    background-color: #f8f9fd;
  }

  .login-wrap {
    position: relative;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0px 10px 34px -15px rgb(0 0 0 / 24%);
  }

  #logo {
    width: 150px;
    height: 150px;
    /*background: #1089ff;*/
    border-radius: 50%;
    font-size: 30px;
    margin: 0 auto;
    margin-bottom: 15px;
  }

  .login-wrap h3 {
    font-weight: 350;
  }


  .col-lg-5 {
    flex: 0 0 auto;
    width: 35%;
  }


  .col h4 {
    text-align: center;
  }

  .active h4 {
    color: green;
    border-bottom: 2px solid green;
  }

</style>